import * as React from 'react';
import * as styles from './SearchBox.module.css';
import { useCallback, useRef } from 'react';
const SearchBox = ({ searchWord, onChange, disabled }) => {
    const inputRef = useRef();
    const handleKeyPress = useCallback((e) => {
        // Prevent escape event from bubbling up if the input is focused and the native reset should be used
        if (e.key === 'Escape') {
            if (inputRef.current.value) {
                e.preventDefault();
                e.stopPropagation();
            }
        }
    }, [inputRef.current]);
    return (React.createElement("input", { ref: inputRef, className: styles.searchBox, type: "search", placeholder: "Search for commands\u2026", autoFocus: true, onChange: onChange, onKeyUp: handleKeyPress, value: searchWord, disabled: disabled }));
};
export default SearchBox;
